import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
// 
const state ={
    position: {},
    userName:''
    // productList:[]
}
// state
const mutations ={
    setPosition(state,val){
        state.position = val
    },
    setUserName(state,val){
        state.userName = val
    }
}
// state
const actions = {
    setPosition({ commit },val){
        // 
        commit('setPosition',val);
    }
}
// vuex
export default new Vuex.Store({
    state,
    mutations,
    actions
})


