<template>
    <div class="">
        <span class="name"></span>
        <!-- select -->
        <m-select 
            :list="provinceList" title="" 
            :value="province" 
            :showWrapperActive="provinceActive"
            className="province"
            @change_active="changeProvActive"
            @chenge="chengeProvince"></m-select>
        <m-select 
            :list="cityList" title="" 
            :value="city" 
            :showWrapperActive="cityActive"
            :disabled="cityDisabled"
            className="city"
            @change_active="changeCityActive"
            @chenge="chengeCity"></m-select>
        <span></span>
        <el-select
            v-model="searchWord"
            filterable
            remote
            reserve-keyword
            placeholder=""
            :remote-method="remoteMethod"
            :loading="loading">
            <el-option
                v-for="item in searchList"
                :key="item"
                :label="item"
                :value="item">
            </el-option>
        </el-select>
        
    </div>
</template>
<script>
import MSelect from './select.vue'
import api from '@/api/index.js'

export default {
    data(){
        return {
            provinceList: ["","","","","","","","","","","",""],          province: "",
            city:"",
            cityList:["","","","","","","","","","","",""],
            cityActive: false,
            provinceActive:false,
            searchList: ["","","","","","","","","","","",""],
            searchWord:'',
            loading:false,
            cityDisabled: true
        }
    },
    components: {
        MSelect
    },
    created(){
        api.getProcinceList().then(res => {
            this.provinceList = res.data.data.map(item => {
                item.name = item.provinceName
                return item
            }) 
            // console.log(this.provinceList);
        })
        
    },
    methods:{
        changeProvActive(flag){
            this.provinceActive = flag
           
            if(flag){
                this.cityActive = false
            }
        },
        changeCityActive(flag){
            this.cityActive = flag
            if(flag){
                this.provinceActive = false
            }
        },
        chengeProvince(value){
            this.province = value.name
            this.cityDisabled = false
            this.cityList = value.cityInfoList
        },
        chengeCity(value){
            this.city = value.name
            this.$store.dispatch('setPosition',value)
            this.$router.push({name: 'index'})
        },
        remoteMethod(val){
            // console.log(this.searchWord,val);
            // 
            
        }
    }
}
</script>



