import axios from 'axios'
// 

axios.defaults.baseURL = "http://api.duyiedu.com/api/meituan"

//
axios.interceptors.request.use(function(config){
    // 
    config.params = {
        ...config.params,
        appkey:'zhengrp_1550991532874'
    }
    return config;
},function(error){
    return Promise.reject(error)
});

export default axios






